// ==== METABAR ==== //

// Style the metabar here.

// 1. Container
// 2. Elements
// 3. Themes
// 4. Headroom

// 1. Container //

.metabar {
  @include ctnr(full);

  background-color: transparent;
  box-shadow: 0 0 1px 0 transparent;
}

.metabar-wrapper {
  @include clearfix;
  @include ctnr(three);

  padding-top: 40px;

}

// 2. Elements //

.metabar-left {
  float: left;
}

.metabar-right {
  float: right;
}

.metabar-logo {
  display: inline-block;

  svg {
    width: 40px;
    height: 40px;
    transition: fill $short;
    vertical-align: middle;
    fill: palette(gray, darker);

    &:hover,
    &:focus {
      fill: palette($primary);
    }
  }
}

.metabar-spacer {
  display: inline-block;
  width: 2px;
  height: 25px;
  margin-right: 11px;
  margin-left: 13px;
  background-color: palette(gray, light);
  vertical-align: middle;
}

.metabar-title {
  transition: color $short;
  color: palette(gray, darker);
  font-family: $sans;
  font-size: 25px;
  font-weight: $sans-bold;
  vertical-align: middle;

  &:hover,
  &:focus {
    color: palette($primary);
  }
}

// 3. Themes //

// Light theme

.metabar.-light {
  .metabar-logo svg {
    fill: palette(white);
  }
}
